<template>
    <!--<Collapse v-model="value1">-->
        <!--<Panel v-for="i in tables">-->
            <!--{{tables[i].name}}-->
            <!--<p slot="content">史蒂夫·乔布斯（Steve Jobs），1955年2月24日生于美国加利福尼亚州旧金山，美国发明家、企业家、美国苹果公司联合创办人。</p>-->
        <!--</Panel>-->
    <!--</Collapse>-->
    <div>
        <Collapse v-model="value1">
            <Panel v-for="i in tables">
                {{i.name}}
                <div slot="content">
                    <div style="width:100%;">
                        <Button type="primary" style="margin-top:0;margin-bottom: 15px;float:left">分类修改</Button>
                        <Button type="error" style="margin-top:0;margin-bottom: 15px;float:left">删除分类</Button>
                    </div>
                    <Table :columns="i.columns" :data="i.data"></Table>
                    <Button type="success" long>提交该分类变更</Button>
                    <!--<div v-for="j in i.data">{{j.Name}}</div>-->
                </div>
            </Panel>
        </Collapse>
    </div>
    <!--<p v-for="i in tables">32432443</p>-->
</template>

<script>
  export default {
    name: 'nav.vue',
    data:()=>{
      return{
        value1:'1',
        tables:{}
      }
    },
    methods:{},
    created () {
      this.$http.get("/console/nav/get").then(function (data){
        data=data.body;
        for(let i in data){
          this.$set(this.tables,i,{
            name:data[i].name,
            columns: [
              {
                title: '名称',
                key: 'Name'
              },
              {
                title: '链接',
                key: 'Url'
              },
              {
                title: '操作',
                key: 'Option',
                render: (h, params) => {
                  return h('div', [
                    h('Button', {
                      props: {
                        type: 'primary',
                        size: 'small'
                      },
                      on: {
                        click: () => {
                          this.change(params.index)
                        }
                      }
                    }, '修改'),
                    h('Button', {
                      props: {
                        type: 'error',
                        size: 'small'
                      },
                      on: {
                        click: () => {
                          this.delete(params.index)
                        }
                      }
                    }, '删除')
                  ])
                }
              }
            ],
            data:[]
          });
          for(let j in data[i].menu){
            this.$set(this.tables[i].data,j,{
              Name:data[i].menu[j].name,
              Url:data[i].menu[j].url
            });
          }
        }
        console.log(this.tables[0])
      },function (err) {
        this.$Modal.error({
          title: '警告',
          content: '服务器发生错误'
        })
      });
    }
  }
</script>

<style scoped>
    .ivu-collapse-content{
        padding:0 !important;
    }
</style>
